// Card.js
import "./card.css";

export const createCard = ({ color = "black", fontSize = "16px" } = {}) => {
  // Create a card container
  const card = document.createElement("div");
  card.className = "card";

  // Create the card title
  const cardTitle = document.createElement("h1");
  cardTitle.className = "card-title";
  cardTitle.textContent = "Hello World";
  cardTitle.style.color = color; // Apply color
  cardTitle.style.fontSize = fontSize; // Apply font size

  // Create the card content
  const cardContent = document.createElement("p");
  cardContent.className = "card-content";
  cardContent.textContent = "Paragraph";
  cardContent.style.color = color; // Apply color
  cardContent.style.fontSize = fontSize; // Apply font size

  card.appendChild(cardTitle);
  card.appendChild(cardContent);

  return card; // Return the card element
};
